import { app } from "./firebaseConfig";
import { getDatabase, ref, get } from "firebase/database";
import { getStorage, ref as storageRef, getDownloadURL } from "firebase/storage"; // Импорт Storage SDK

// Инициализируем приложение Firebase
const db = getDatabase(app);
const storage = getStorage(app); // Инициализация Storage SDK

// Функция для получения объекта из корневого узла Firebase
async function getObjectFromRoot() {
  try {
    const rootRef = ref(db, "/"); // Пустая строка указывает на корень базы данных
    const snapshot = await get(rootRef);

    if (snapshot.exists()) {
      // Получаем значение корневого объекта
      const rootData = snapshot.val();
    console.log(Object.keys(rootData))


      // Обновляем каждый элемент массива rootData, добавляя ссылку на изображение из Firebase Storage
      const updatedRootData = await Promise.all(
        Object.keys(rootData).map(async (key) => {
          const item = rootData[key];

          // Создаем ссылку на файл в Storage, используя имя картинки "ap" + [id статьи] + '.png'
          const imagePath = `gs://octup-test.appspot.com/apView/ap${item.articleId}.png`;
          const imageRef = storageRef(storage, imagePath);

          try {
            // Получаем URL загруженной картинки
            const imageUrl = await getDownloadURL(imageRef);

            // Обновляем объект, добавляя поле "imageUrl" с ссылкой на изображение
            return { ...item, imageUrl };
          } catch (error) {
            // Если возникла ошибка при получении URL картинки, просто возвращаем объект без поля "imageUrl"
            return item;
          }
        })
      );

      return updatedRootData;
    } else {
      console.log("Объекты не найдены");
      return null;
    }
  } catch (error) {
    console.error(error);
    return null;
  }
}

export default getObjectFromRoot;
